import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import "./App.css";

import CreateNote from "./components/CreateNote";
import CreateUser from "./components/CreateUser";
import NoteList from "./components/NoteList";
import Adrian from "./components/Adrian";
import Slide from "./components/Slide";
import Nav from "./components/Nav";

function App() {
  return (
    <Router basename="/reactexample">
      <Nav />
      <Slide />
      <Route exact path="/reactexample" component={NoteList} />
      <Route path="/reactexample/notas" component={CreateNote} />
      <Route path="/reactexample/usuario" component={CreateUser} />
      <Route path="/reactexample/home" component={Adrian} />
    </Router>
  );
}

export default App;
